<template>
  <div class="footerbar">
    <div class="icon">
      <i class="iconfont icon-shop"></i>
      <span class="text">店铺</span>
    </div>
    <div class="icon">
      <i class="iconfont icon-service"></i>
      <span class="text">客服</span>
    </div>
    <div class="icon">
      <i class="iconfont icon-collect"></i>
      <span class="text">收藏</span>
    </div>
    <div class="add">加入购物车</div>
    <div class="buy">马上抢</div>
  </div>
</template>

<script>
export default {
  name: "ProductFooter"
}
</script>

<style scoped lang="scss">
  .footerbar {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    .add,
    .buy {
      width: 30%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      color: #fff;
    }
    .add {
      background-color: #ff9500;
    }
    .buy {
      background-color: #ff0036;
    }
    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-size: 12px;
      color: #666;
      flex: 1;
      .iconfont {
        font-size: 20px;
      }
    }
  }
</style>


