<template>
    <div class="product">
      <header class="header">
        <product-header/>
      </header>
      <me-scroll ref="scroll" scrollbar :flag="flags">
        <product-slide :id="this.$route.params.id" :sliders="sliders" @slidersLoaded="ready('sliders')"/>
        <product-desc :content="content"  @contentLoaded="ready('content')"/>
        <product-recommend :recommends="recommends"  @recommendsLoaded="ready('recommends')"/>
        <product-seller :seller="seller"  @sellerLoaded="ready('seller')"/>
      </me-scroll>
      <div class="g-footer-container">
        <product-footer></product-footer>
      </div>
    </div>
</template>

<script>
  import {getProductDetails} from 'api/product';
  import ProductHeader from './productHeader';
  import MeScroll from 'base/scroll';
  import ProductSlide from './productSlide';
  import ProductDesc from './description';
  import ProductRecommend from './recommends.vue';
  import ProductSeller from './seller';
  import ProductFooter from './productFoot';
  export default {
    name: 'Product',
    components: {
      ProductHeader,
      MeScroll,
      ProductSlide,
      ProductDesc,
      ProductRecommend,
      ProductSeller,
      ProductFooter
    },
    data() {
      return {
        sliders: [],
        content: {},
        recommends: {},
        seller: {},
        flags: []
      };
    },
    created() {
      this.getdata(this.$route.params.id);
      
    },
    methods: {
      getdata(url) {
        return getProductDetails(url).then(res => {
          console.log(res);
          const apiStack = JSON.parse(res.apiStack[0].value);
          this.sliders = res.item.images;
          this.content = {
            title: res.item.title,
            price: apiStack.price.price.priceText,
            soldCountTotal: apiStack.item.vagueSellCount,
            soldCountMonth: apiStack.item.sellCount,
            from: apiStack.delivery.from,
            deliveryFade: apiStack.delivery.postage
          };
          this.recommends = {
            totalCount: res.rate.totalCount,
            keywords: res.rate.keywords,
            user: res.rate.rateList[0]
          };
          this.seller = {
            brandPic: res.seller.creditLevelIcon,
            shopName: res.seller.shopName,
            shopPic: res.seller.shopIcon,
            evaluates: res.seller.evaluates
          }

          setTimeout(()=>{
            this.$refs.scroll.update();
          },1000)
        });
      },
      ready (data) {
        console.log('------------');
        console.log(data);
        this.flags.push(1);
        console.log(this.flags);
      }
    }
  };
</script>

<style scoped lang="scss">
  @import "~assets/scss/mixins";
  .product {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    z-index: $product-z-index;
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
    color: black;
    .header {
      position: fixed;
      width: 100%;
      z-index: $product-z-index + 1;
      opacity: .9;
    }
  }
</style>


