<template>
  <me-navbar class="header" title="商品详情">
    <i class="iconfont icon-back" slot="left" @click="back"></i>
    <div slot="center"></div>
    <i class="iconfont icon-cart" slot="right"></i>
  </me-navbar>
</template>

<script>
  import MeNavbar from 'base/navbar';
  export default {
    components: {
      MeNavbar
    },
    methods: {
      back() {
        this.$router.back();
      }
    }
  }
</script>

<style scoped lang="scss">
  .header {
    color: #fff;
    background-color: rgba(255,0,0,1);
    .iconfont.icon-back,
    .iconfont.icon-cart {
      font-size: 20px;
    }
  }
</style>


