<template>
  <div class="desc">
    <div class="price-wrap">
      <span class="price">￥{{content.price}}</span>
      <span class="soldNum">{{content.soldCountMonth}}件已售</span>
    </div>

    <div class="title">
      <div class="wrap">{{content.title}}</div>
    </div>

    <div class="delivery">
      <span class="fare">{{content.deliveryFade}}</span>
      <span class="count">月售{{content.soldCountTotal}}件</span>
      <span class="from">{{content.from}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductDesc',
  props: {
    content: {
      type: Object,
      default: {}
    }
  },
  watch: {
    content(data) {
      if (Object.keys(data).length !== 0) {
        this.$emit('contentLoaded');
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "~assets/scss/mixins";
  .desc{
    width: 100%;
    margin-bottom: 10px;
  }
  .price-wrap {
    height: 45px;
    background: linear-gradient(to right, #ef3487 0%, #ef355f 100%);
    color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    @include flex-between();
    .price {
      font-size: 18px;
    }
    .soldNum {
      font-size: 14px;
      display: block;
      padding: 8px 15px;
      border-radius: 5px;
      background-color: #cc2e3e;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .title {
    height: 38px;
    display: flex;
    align-items: center;
    font-size: 14px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #fff;
    .wrap {
      @include ellipsis();
    }
  }
  .delivery {
    height: 26px;
    font-size: 13px;
    color: #999;
    padding-left: 10px;
    padding-right: 10px;
    @include flex-between();
    border-top: 1px solid #fcfcfc;
    background-color: #fff;
  }
</style>


