<template>
  <div class="seller">
    <div class="left">
      <div class="pic-wrap">
        <img :src="seller.shopPic"/>
      </div>
    </div>
    <div class="right">
      <div class="shopName">{{seller.shopName}}</div>
      <div class="TMallIcon">
        <img :src="seller.brandPic"/>
      </div>
      <div class="scoreList">
        <div class="score-desc">{{seller.evaluates[0].title}}{{seller.evaluates[0].score}}</div>
        <div class="score-seller">{{seller.evaluates[1].title}}{{seller.evaluates[1].score}}</div>
        <div class="score-delivery">{{seller.evaluates[2].title}}{{seller.evaluates[2].score}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ProductSeller',
    props: {
      seller: {
        type: Object,
        default: {}
      }
    },
    watch: {
      seller(data) {
        if (Object.keys(data).length !== 0) {
          this.$emit('sellerLoaded');
        }
      }
    }
  }
</script>

<style scoped lang="scss">
 @import "~assets/scss/mixins";
 .seller {
    margin-top: 10px;
    background-color: #fff;
    padding: 10px 10px 20px;
    display: flex;
    .left {
      width: 50px;
      margin-right: 8px;
      .pic-wrap {
        width: 50px;
        height: 50px;
        border: 1px solid #ccc;
        img {
          width: 100%;
        }
      }
     
    }
    .right {
      flex: 1;
      .scoreList {
        @include flex-between();
        padding-right: 55px;
        padding-top: 25px;
        color: #666;
      }
      .shopName {
        padding-top: 10px;
        font-size: 14px;
      }
      .TMallIcon {
        width: 28px;
        padding-top: 3px;
        img {
          width: 100%;
        }
      }
    }
 }
</style>


