<template>
  <div class="p-recommend">
    <div class="re-total">商品评价({{recommends.totalCount}})</div>
    <ul class="re-bubble">
      <li class="re-item" v-for="(item, index) in recommends.keywords" :key="index">
        <span class="re-item-text">{{item.word}}</span>
        <span  class="re-item-num">({{item.count}})</span>
      </li>
    </ul>

    <div class="user">
      <div class="user-info">
        <!-- <div class="user-pic"><img :src="recommends.user.headPic"/></div> -->
        <div class="user-name">{{recommends.user.userName}}</div>
      </div>
      <div class="user-re">{{recommends.user.content}}</div>
      <div class="re-info">{{recommends.user.dateTime}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductRecommend',
  props: {
    recommends: {
      type: Object,
      default: {}
    }
  },
  method:{
    
  },
  watch: {
    recommends(data) {
      if (Object.keys(data).length !== 0) {
        this.$emit('recommendsLoaded');
        
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "~assets/scss/mixins";
  .p-recommend {
    padding: 10px;
    background-color: #fff;
    .re-total {
      font-size: 14px;
      line-height: 1.5em;
      color: #666;
    }
    .re-bubble {
      display: flex;
      flex-wrap: wrap;
      .re-item {
        display: flex;
        padding: 5px 8px;
        background-color: #ffeeee;
        border-radius: 10px;
        font-size: 12px;
        margin-right: 5px;
        margin-top: 8px;
        color: #666;
      }
    }
    .user {
      padding-top: 20px;
      .user-info {
        display: flex;
        align-items: center;
        font-size: 12px;
        padding-bottom: 10px;
        .user-pic {
          width: 24px;
          height: 24px;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 100%;
          }
        }
        .user-name {
          color: #666;
          padding-left: 5px;
        }
      }
      .user-re {
        line-height: 1.2em;
      }
      .re-info {
        color: #666;
        padding-top: 10px;
        font-size: 12px;
      }
    }
  }
</style>


